.MemoryCard {
  display: inline-block;
  width: 150px;
  height: 80px;
  margin: 5px;
  cursor: pointer;
  position: relative;
  perspective: 800px
}

.MemoryCardInner {
    width: 100%;
    height: 100%;
    position: absolute;
    transform-style: preserve-3d;
    transition: 0.8s;
    transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
    border-radius: 5px;
    border: 3px solid navy;
    box-shadow: 0 6px 16px rgba(0,0,0,0.15)
}

.MemoryCardInner.flipped {
    transform: rotateY(180deg)
}

.MemoryCardFront {
    font-size: 30px;
    padding-top: 20px;
    transform: rotateY(180deg);
}

.MemoryCardBack {
  background: repeating-radial-gradient(circle,
    purple,
    purple 10px,
    #4b026f 10px,
    #4b026f 20px);
}

.MemoryCardBack img {
  width: 60px;
  padding-top: 27px
}

.MemoryCardBack, .MemoryCardFront{
    position:absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden
}